import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { env } from 'src/app/config/env';
import { MVisor } from 'src/app/pages/visor/models/visor.class';
import { PrmGenerales } from 'src/app/_class';

@Injectable({
	providedIn: 'root',
})
export class GeneralesService {
	constructor(private http: HttpClient) {}
	prmGen: PrmGenerales;
	DatosVisor: MVisor;
	resAPI: any;

	// Guardado de datos distribución de planta
	resulAPI: any;

	getDatosUN(): Observable<any> {
		let url = '/api/XPro/QueryGen?sqlQuery=';
		url =
			url +
			"SELECT ID_UN,NOMBRE,ID_LEGAL FROM UNIDADES_NEGOCIOS WHERE ID_UN_SUPERIOR='RAIZ'";

		return this.http.get(url).pipe(
			map((vec: any) => {
				this.prmGen = vec.datos;
				return vec.datos;
			}),
			catchError((err) => {
				return throwError(err);
			})
		);
	}

	getGenUN(): any {
		return this.prmGen;
	}
	async setExecAPI(Accion: string, PrmDatos: any): Promise<any> {
		// define sintaxis de envio
		const httpOptions = {
			headers: new HttpHeaders({
				'Content-Type': 'application/json',
				Accept: 'application/json',
			}),
		};
		const url =  `${env.urlNET}/api/XPro/AccionSQL`;
		const prmAPI = {
			prmAccion: Accion,
			prmDatos: JSON.stringify(PrmDatos),
		};
		await this.http
			.post(url, JSON.stringify(prmAPI), httpOptions)
			.toPromise()
			.then((result) => (this.resAPI = result))
			.catch((error) => console.log(error));
		return JSON.parse(this.resAPI); // you can return what you want here
	}
}
