import { fn } from '@angular/compiler/src/output/output_ast';
import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot } from '@angular/router';
import { LoginService } from 'src/app/service/service.index';
import { fnLocal } from '../../_function/localStorage.fn';
import { MenuService } from './menu.service';
@Injectable()
export class AuthService {
  constructor(private router: Router) {}
  loggedIn: boolean = false;

  logIn(status, token) {
    fnLocal.agregar('token', token);
    this.loggedIn = status;
    this.router.navigate(['/dashboard']);
  }

  logOut() {
    this.loggedIn = false;
    fnLocal.remove('token');
    fnLocal.remove('menu');
    this.router.navigate(['/login']);
  }

  isLoggedIn(): boolean {
    return this.loggedIn;
  }
}

@Injectable()
export class AuthGuardService implements CanActivate {
  constructor(
    private router: Router,
    private authService: AuthService,
    private login: LoginService,
    private menu: MenuService
  ) {}
  canActivate(route: ActivatedRouteSnapshot): boolean {
    this.menu.list().subscribe((resp: any[]) => {
      fnLocal.agregar('menu', resp);
    });
    const isLoggedIn = this.authService.isLoggedIn();
    let aa: any = (async () => {
      const isLoginForm = route.routeConfig.path === 'login';
      console.log(route.routeConfig.path);
      return !isLoggedIn ? await this.login.validateToken() : isLoggedIn;
    })();

    // if (isLoggedIn && isLoginForm) {
    //   this.router.navigate(['/']);
    //   return false;
    // }

    // if (!isLoggedIn && !isLoginForm) {
    //   this.router.navigate(['/login']);
    // }
    console.log(this.authService.isLoggedIn());
    // return isLoggedIn || isLoginForm;
    return true;
  }
}
