import { Component, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { ProductionParametersService } from 'src/app/service/service.index';
import { Config, MenuOpction } from '../../_class';
import { ClassCif } from 'src/app/_class/bcosto.class';

@Component({
	selector: 'app-cif',
	templateUrl: './cif.component.html',
	styleUrls: ['./cif.component.scss'],
})
export class CifComponent implements OnInit {
	selectedRowIndex = -1;
	@ViewChild('DataGrid', { static: false })
	ccgridContainer: DxDataGridComponent;
	menuCrud: any;
	constructor(private _serviceCif: ProductionParametersService) {}
	data: ClassCif[] = [];
	Config: Config = MenuOpction;
	isLoading = true;
	loadPanelPosition = { of: '#gridContainer' };
	ngOnInit() {
		this.getCif();
	}

	getCif() {
		this._serviceCif.getCif().subscribe((resp: ClassCif[]) => {
			this.data = resp;
			this.isLoading = false;
			console.log(resp);
		});
	}
	add(e) {
		console.log('<<<Edit ->>>>>>', e);
	}
	update(e) {}
	selectedChanged(e) {
		this.selectedRowIndex = e.component.getRowIndexByKey(
			e.selectedRowKeys[0]
		);
	}

	selectNavegate({ type }) {
		console.log(type);
		if (type === 'buscar') {
			this.Config.save.VP = true
			this.Config.save.OP = true
			console.log('permitido');
			return;
		} else if (type === 'save') {
			return;

		}
	}
}
