import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DxScrollViewComponent } from 'devextreme-angular';
import { SeccionesService } from 'src/app/service/service.index';
import { Config, MenuOpction } from 'src/app/_class';
import { ClassEmpleados, ClassSecciones } from 'src/app/_class/bcosto.class';
import notify from 'devextreme/ui/notify';
import { DxTreeListModule, DxTreeListComponent } from 'devextreme-angular';
@Component({
	selector: 'app-secciones',
	templateUrl: './secciones.component.html',
	styleUrls: ['./secciones.component.scss'],
})
export class SeccionesComponent implements OnInit {
	@ViewChild('scrollView', { static: false })
	scrollView: DxScrollViewComponent;
	@ViewChild(DxTreeListComponent, { static: false })
	treeList: DxTreeListComponent;
	popupVisible = false;
	myForm: FormGroup;
	selectedRowIndex = -1;
	labelLocation: string;
	readOnly: boolean;
	showColon: boolean;
	minColWidth: number;
	colCount: number;
	width: any;
	Empleados: ClassEmpleados[] = [];
	Empresa!: ClassSecciones;
	seccionesEmpresa: ClassSecciones[] = [];
	usuarioSelect: ClassSecciones;
	EditSelect = false;
	optionTypeSelect = '';
	Config: Config = MenuOpction;
	filterValue: Array<any>;
	submitted = false;
	dataSource1 = ['ACTIVO', 'INACTIVO'];
	showMe = false;
	SelectAction = '';
	activeRowSelect = true;
	constructor(private _service: SeccionesService, public fb: FormBuilder) {
		// for(const a in this.Config) {
		// 	if(this.Config[a].OP) {
		// 		this.Config[a].OP = false
		// 		// console.log(= false)
		// 	}TODO: esto nos ayuda a recorreo un objeto
		// }
	}
	ngOnInit(): void {
		this.getSecciones();
		this.myForm = this.fb.group({
			ITEM: ['', Validators.required],
			ID_SECCION: ['', Validators.required],
			DESCRIPCION: ['', Validators.required],
			ANTERIOR: ['', Validators.required],
			ESTADO: ['', Validators.required],
			ASIGNABLE: ['', null],
			INVENTARIO: ['', null],
			MANO_OBRA: ['', null],
			ID_UN: [''],
			ID_UN_ITEM: [''],
			TIPO: [''],
			MSG: [''],
			parentId: [''],
			items: [null],
		});
	}
	get f(): any {
		return this.myForm.controls;
	}
	getSecciones(): void {
		this._service.getServicios().subscribe((resp: ClassSecciones[]) => {
			const ID =
				this.Empresa !== undefined ? this.Empresa.ITEM : resp[0].ITEM;

			this.treeList.instance.selectRows([ID], true).then((empre) => {
				this.Empresa = empre[0];
			});

			this.seccionesEmpresa = resp;
		});
	}
	screen(width): any {
		return width < 700 ? 'sm' : 'lg';
	}
	SelectRow({ selectedRowsData }): void {
		this.Empresa = selectedRowsData[0];
		this.Empresa.items = null;
		this.EditSelect = false;
		this.treeList.instance.selectRows([this.Empresa.ITEM], true);
		this.myForm.setValue(this.Empresa);
		this.Config.add.OP = true;

		if (this.Empresa.TIPO === 'Planta') {
			this.Config.edit.OP = false;
		} else {
			this.Config.save.OP = false;
			this.Config.cancelar.OP = false;
			this.Config.InputBuscar.OP = false;
			this.Config.buscar.OP = true;
			this.Config.edit.OP = true;
		}
		this.ListEmpleados();
	}
	valueChanged(dd): void {
		console.log(dd);
	}
	ListEmpleados(): void {
		this._service
			.getEmpleados(this.myForm.value.ID_SECCION)
			.then((resp: any) => {
				this.Empleados = resp;
			})
			.catch((err) => {
				console.log(err);
			});
	}
	resetFormulario(): void {
		const aa: any = this.treeList.instance.getSelectedRowsData();
		aa[0].items = null;
		this.myForm.setValue(aa[0]);
	}
	nuevoFormulario(): void {
		this.myForm.setValue({
			ITEM: '',
			ID_SECCION: '',
			DESCRIPCION: '',
			ANTERIOR: '',
			MANO_OBRA: '',
			ESTADO: '',
			ASIGNABLE: '',
			INVENTARIO: '',
			ID_UN: '',
			ID_UN_ITEM: '',
			TIPO: '',
			MSG: '',
			parentId: '',
		});
	}
	deselectRows(keys): void {
		this.treeList.instance.deselectRows(keys);
	}
	deselectVisibleRows(): void {
		this.treeList.instance.deselectAll();
	}
	keyPress(event: KeyboardEvent): any {
		// tslint:disable-next-line: deprecation
		return event.charCode >= 48 && event.charCode <= 57;
	}

	procesaPropagar({ type, text = null }): void {
		console.log(type);
		this.optionTypeSelect = type;
		switch (type) {
			case 'Nuevo':
				this.Config.save.OP = true;
				this.Config.cancelar.OP = true;
				this.Config.buscar.OP = false;
				this.Config.add.OP = false;
				this.Config.edit.OP = false;
				this.myForm.setValue({
					ITEM: '',
					ID_SECCION: this.Empresa.ID_SECCION,
					DESCRIPCION: '',
					ANTERIOR: this.Empresa.ITEM,
					MANO_OBRA: '',
					ESTADO: '',
					ASIGNABLE: '',
					INVENTARIO: '',
					ID_UN: '',
					ID_UN_ITEM: '',
					TIPO: '',
					MSG: '',
					parentId: '',
					items: null,
				});
				this.EditSelect = true;
				this.SelectAction = type;
				break;
			case 'cancelar':
				this.Config.add.OP = true;
				this.Config.save.OP = false;
				this.Config.cancelar.OP = false;
				this.Config.InputBuscar.OP = false;
				this.Config.buscar.OP = true;
				this.Config.edit.OP = false;
				this.EditSelect = false;
				this.getSecciones();
				this.resetFormulario();
				this.submitted = false;
				this.activeRowSelect = false;
				break;
			case 'editar':
				this.SelectAction = type;
				if (this.myForm.value.TIPO !== 'Planta') {
					this.EditSelect = !this.EditSelect;
					this.Config.save.OP = true;
					this.Config.edit.OP = false;
					this.Config.buscar.OP = false;
					this.Config.add.OP = false;
					this.Config.cancelar.OP = true;
				} else {
					notify(
						`No Puedes Editar esta ${this.myForm.value.TIPO}`,
						'error',
						2000
					);
				}
				break;
			case 'update':
				this.getSecciones();
				this.ListEmpleados();

				break;
			case 'buscar':
				this.Config.cancelar.OP = true;
				this.Config.InputBuscar.OP = true;
				this.Config.buscar.OP = false;

				break;
			case 'guardar':
				console.log('----------->>> ', this.SelectAction);
				console.log(this.Empresa);
				console.log(this.myForm.invalid);

				this.submitted = true;
				if (this.myForm.invalid) {
					return;
				}

				if (this.SelectAction === 'editar') {
					console.log(this.myForm.value);
					const datos = this.myForm.value;
					datos.ID_SECCION_ANTERIO = this.Empresa.ID_SECCION;
					this._service.edit(datos).subscribe((resp: any) => {
						if (resp.ok) {
							this.Config.add.OP = true;
							this.Config.save.OP = false;
							this.Config.cancelar.OP = false;
							this.Config.InputBuscar.OP = false;
							this.Config.buscar.OP = true;
							this.Config.edit.OP = true;
							this.EditSelect = false;
							this.getSecciones();
							notify(
								`${this.myForm.value.TIPO} fue ${this.SelectAction}`,
								'success',
								2000
							);
						} else {
							notify(
								`No Puedes Editar esta ${resp.message}`,
								'error',
								2000
							);
						}
					});
				} else if (this.SelectAction === 'Nuevo') {
					this._service.add(this.myForm.value).subscribe((resp) => {
						console.log(resp);
						this.getSecciones();
						notify(`Nuevo creado`, 'success', 2000);
					});
				}
				break;
			case 'txtSearch':
				this.filterValue = ['DESCRIPCION', 'startswith', [text]];
				// console.log('Estas Buscando', text)
				this.procesaPropagar({ type: 'reset' });
				this.resetFormulario();
				break;
			default:
				this.Config.add.OP = true;
				this.Config.save.OP = false;
				this.Config.cancelar.OP = false;
				this.Config.InputBuscar.OP = false;
				this.Config.buscar.OP = true;
				this.Config.edit.OP = true;
				this.EditSelect = false;
				break;
		}
	}
}
