import {
	Component,
	EventEmitter,
	Input,
	OnInit,
	Output,
	ViewChild,
} from '@angular/core';
import { Observable } from 'rxjs';
import { Config, MenuOpction } from 'src/app/_class';
// tslint:disable-next-line: class-name

@Component({
	selector: 'app-navigation-crud',
	templateUrl: './navigation-crud.component.html',
	styleUrls: ['./navigation-crud.component.scss'],
})
export class NavigationCrudComponent implements OnInit {
	@Input() _Config: Config = MenuOpction;
	@Output()
	propagar = new EventEmitter<any>();
	ngOnInit(): void {}
	action(type): void {
		this.propagar.emit({ type });
	}
	input(key, text): void {
		if (key === 'Enter') {
			this.propagar.emit({ type: 'txtSearch', text });
		}
	}
}
