import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductoService } from 'src/app/service/service.index';
import { DProducto, IData } from 'src/app/_class';

@Component({
  selector: 'app-productos',
  templateUrl: './productos.component.html',
  styleUrls: ['./productos.component.scss'],
})
export class ProductosComponent implements OnInit {
  productosData: any;
  dataSource: DProducto[] = [];
  url: string;

  constructor(
    private _ProducS: ProductoService,
    private router: Router,
    // private IDatosPro: IData
  ) {}

  ngOnInit(): void {
    this._ProducS.get().subscribe((resp: any) => {
      console.log(resp);
      this.dataSource = resp;
    });
  }

  DetalleProducto(e, data): void {
    // this.IDatosPro.storage = data.data;
    // this.router.navigate(['detpro'], { queryParams: { proDat: JSON.stringify(data.data) }} );
    this.router.navigate(['detpro']);
  }
}
