import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { env } from '../../config/env';
import notify from 'devextreme/ui/notify';
import { fnLocal } from 'src/app/_function/localStorage.fn';
@Injectable({
	providedIn: 'root',
})
export class LoginService {
	status = false;
	constructor(private http: HttpClient) {}

	getEmpresa(): Observable<any> {
		const url = `${env.url1}/empresas`;
		return this.http.post(url, { action: 'get_empresa', pass: '' }).pipe(
			map((resp: any) => resp.data),
			catchError((err) => throwError(err))
		);
	}
	initSession(user): Observable<any> {
		const url = `${env.url1}/login`;

		return this.http.post(url, user).pipe(
			catchError((err: any) => {
				notify(err, 'error', 2000);
				return throwError(err.error);
			})
		);
	}
	validateToken(): Promise<any> {
		const url = `${env.url1}/login/auth`;
		return this.http
			.get(url, { headers: { token: fnLocal.get('token') } })
			.toPromise();
	}
}
