import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { env } from '../../config/env';
import { catchError, map } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { fnLocal } from 'src/app/_function/localStorage.fn';
@Injectable({
	providedIn: 'root',
})
export class ProductionParametersService {
	constructor(private http: HttpClient) {}

	getManosObra(): Promise<any> {
		const url = `${env.url1}/manoObra`;
		return this.http
			.get(url, { headers: { token: fnLocal.get('token') } })
			.toPromise();
	}
	getUndedida(): Promise<any> {
		const url = `${env.url1}/umedida`;
		return this.http
			.get(url, { headers: { token: fnLocal.get('token') } })
			.toPromise();
	}
	getCif(): Observable<any> {
		const url = `${env.url1}/cif`;
		return this.http
			.get(url, { headers: { token: fnLocal.get('token') } })
			.pipe(
				map((resp: any) => resp.data),
				catchError((err) => throwError(err))
			);
	}
}

