
import { Component, HostBinding } from '@angular/core';
import { ActivationEnd, Router } from '@angular/router';
import { filter, map } from 'rxjs/operators';
import { fnLocal } from 'src/app/_function/localStorage.fn';
import { AuthService, ScreenService, AppInfoService } from '../../shared/services';
@Component({
  selector: 'app-init',
  templateUrl: './init.component.html',
  styles: []
})
export class InitComponent  {
  @HostBinding('class') get getClass() {
    return Object.keys(this.screen.sizes).filter(cl => this.screen.sizes[cl]).join(' ');
  }
  title:string = '';
  constructor(
      private authService: AuthService
    , private screen: ScreenService
    , public appInfo: AppInfoService
    , private router: Router
    // , private s: SocketService
    ) {
      let user = fnLocal.get('token')
      // this.s.ejecuteInit(user.token)
      this.title = this.appInfo.title()
      
      // this.s.in.connect();
      // s.in._on('my-info').subscribe((resp:any) => {
      //   console.table("my info", resp);
        
      // })
    }

  isAutorized() {
    return this.authService.isLoggedIn();
  }

}
