import { Component, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { ProductionParametersService } from 'src/app/service/service.index';
import { ManoObra } from '../../_class/bcosto.class';
@Component({
	selector: 'app-mano-obra',
	templateUrl: './mano-obra.component.html',
	styleUrls: ['./mano-obra.component.scss'],
})
export class ManoObraComponent implements OnInit {
	@ViewChild(DxDataGridComponent, { static: false })
	aagridContainer: DxDataGridComponent;

	datos: ManoObra[] = [];
	isLoading = false;
	loadPanelPosition = { of: '#gridContainer' };
	seelctTipo = ['$', '%'];
	seelctEstados = ['ACTIVO', 'INACTIVO'];
	SelectUm: any[] = [];
	constructor(
		private _cservice: ProductionParametersService // , private s: SocketService
	) {}
	selectedRowIndex = -1;
	ngOnInit() {
		this.getList();
		this.getUnidadMEdida();
		// this.initSocket()
	}
	async getList(): Promise<void> {
		this.isLoading = true;

		try {
			const { data, ok } = await this._cservice.getManosObra();
			if (ok) {
				this.datos = data;
				this.isLoading = false;
			} else {
				this.datos = [];
				this.isLoading = true;
			}
		} catch (error) {
			console.error('->>>>>>', error);
		}
		// this.s.in.emit('consulta', 'Mano_de_obra', (resp: any) => {
		//   console.log('mano_de_obra', resp);
		//   this.isLoading = false
		//   this.datos = resp;

		// })
	}
	getUnidadMEdida() {
		// this.s.in.emit('consulta', 'unidad_medida', (resp: any) => {
		//   console.log('unidad_medida', resp);
		//   this.SelectUm = resp
		// })
	}
	onInitNewRow(e) {
		console.log(e.data);
	}
	update(e) {
		console.log(e);
	}

	selectedChanged(e) {
		this.selectedRowIndex = e.component.getRowIndexByKey(
			e.selectedRowKeys[0]
		);
	}
	// initSocket(){
	//   const socket = socketIo('http://192.168.10.11:6001')
	//   socket.on('hello', (resp => {
	//     console.log(resp);
	//   }))
	// }
}
