import {
  Component,
  NgModule,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import { CommonModule } from '@angular/common';

import { AuthService } from '../../services';

import { ActivationEnd, Router } from '@angular/router';
import { DevexpressModule } from 'src/app/devexpress/devexpress.module';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'app-header',
  templateUrl: 'header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent {
  constructor(private authService: AuthService, public router: Router) {
    this.getTitlePagina();
  }

  @Output()
  menuToggle = new EventEmitter<boolean>();

  @Input()
  menuToggleEnabled = false;

  title: string;

  userMenuItems = [
    {
      text: 'Profile',
      icon: 'user',
      onClick: () => {
        this.router.navigate(['profile']);
      },
    },
    {
      text: 'Logout',
      icon: 'runner',
      onClick: () => {
        // this.s.in.disconnect()
        this.authService.logOut();
      },
    },
  ];

  toggleMenu = () => {
    this.menuToggle.emit();
  };
  getTitlePagina() {
    this.router.events
      .pipe(
        filter((event) => event instanceof ActivationEnd),
        filter(
          (event) => (event as ActivationEnd).snapshot.firstChild === null
        ),
        map((event) => (event as ActivationEnd).snapshot.data)
      )
      .subscribe((children) => (this.title = children.title));
  }
}
