import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { fnLocal } from 'src/app/_function/localStorage.fn';
import { env } from '../../config/env';
@Injectable({
	providedIn: 'root',
})
export class SeccionesService {
	constructor(private http: HttpClient) {}

	getServicios(): Observable<any> {
		const url = `${env.url1}/seccion`;
		return this.http.get(url, { headers: { token: fnLocal.get('token') } });
	}

	getEmpleados(id_seccion: any): Promise<any> {
		const url = `${env.url1}/seccion/empleado/${id_seccion}`;
		return this.http
			.get(url, { headers: { token: fnLocal.get('token') } })
			.toPromise();

		// pipe(
		//   map((resp: any) => resp.data),
		//   catchError(err => throwError(err)))
	}
	add(data): Observable<any> {
		const url = `${env.url1}/seccion`;
		return this.http
			.post(url, data, { headers: { token: fnLocal.get('token') } })
			.pipe(catchError((err) => throwError(err)));
	}
	edit(data): Observable<any> {
		const url = `${env.url1}/seccion`;
		return this.http
			.put(url, data, { headers: { token: fnLocal.get('token') } })
			.pipe(catchError((err) => throwError(err)));
	}
}
