import { Component, OnInit, ViewChild } from '@angular/core';
import { LoginService } from 'src/app/service/service.index';
import { AppInfoService, AuthService } from 'src/app/shared/services';
import notify from 'devextreme/ui/notify';
import { DxFormComponent } from 'devextreme-angular';
const sendRequest = (value) => {
	const validEmail = 'test@dx-email.com';
	return new Promise((resolve) => {
		setTimeout(() => {
			resolve(value === validEmail);
		}, 1000);
	});
};
@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
	@ViewChild('Myformulary', { static: false }) form: DxFormComponent;
	public id_usuario = '';
	password = '';
	code_empresa: any;
	Empresas: any[];
	data: any;
	buttonOptions: any = {
		text: 'Register',
		type: 'success',
		useSubmitBehavior: true,
		width: '100%',
	};
	titleEmpresa: string;
	constructor(
		private authService: AuthService,
		public appInfo: AppInfoService,
		// tslint:disable-next-line: variable-name
		private _login: LoginService
	) {
		this.titleEmpresa = appInfo.title();
	}

	ngOnInit(): void {
		// this.socket.on('getEmplesa', (resp: any  )=> {

		// })
		// tslint:disable-next-line: deprecation
		this._login.getEmpresa().subscribe((resp) => {
			this.Empresas = resp;
		});
	}
	onLoginClick(args): any {
		if (!args.validationGroup.validate().isValid) {
			return;
		}
		const user = {
			cedula: this.id_usuario,
			password: this.password,
			code_empresa: this.code_empresa,
		}; // tslint:disable-next-line: deprecation
		this._login
			.initSession(user)
			.subscribe(({ data, message, ok, token }: any) => {
				if (ok) {
					notify('Bienvenidos a Artdecon', 'success', 3000);
					this.authService.logIn(ok, token);
				} else {
					notify(message, 'error', 3000);
				}
			});
		// args.validationGroup.reset();
	}
	asyncValidation(params): any {
		return sendRequest(params.value);
	}
	onKeyPress({ event }): any {
		const { code } = event;
		console.log(code);

		if (code === 'Tab') {
			console.log(this.data);
		}
	}
	onFormSubmit = (e) => {
		const {ID_USUARIO, password} = this.form.instance.option('formData');
		const user = {
			cedula: ID_USUARIO,
			password,
			code_empresa: this.code_empresa,
		}; // tslint:disable-next-line: deprecation
		this._login.initSession(user).subscribe(({ data, message, ok, token }: any) => {
				console.log(ok,data )
				if (ok) {
					notify(
						{
							message: 'You have submitted the form',
							position: {
								my: 'center top',
								at: 'center top',
							},
						},
						'success',
						3000
					);
					this.authService.logIn(ok, token)
				} else {
					notify(message, 'error', 3000);
				}
			});

		e.preventDefault();
	}
	passwordComparison = () => {
		return this.form.instance.option('formData').Password;
	}
}
