import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { fnLocal } from 'src/app/_function/localStorage.fn';
import { env } from '../../config/env';
@Injectable({
  providedIn: 'root',
})
export class FnUserService {
  constructor(private http: HttpClient) {}

  getDominios(): Observable<any> {
    const url = `${env.url1}/bcostos/dominios`;
    return this.http
      .get(url, { headers: { token: fnLocal.get('token') } })
      .pipe(catchError((err) => throwError(err)));
  }

  updateDominios(item, clase, des): Promise<any> {
    const url = `${env.url1}/bcostos/dominios`;
    return this.http
      .put(url, {
        code: item,
        udclase: clase,
        ud_descticcion: des,
      }, {headers: {token: fnLocal.get('token')}} )
      .toPromise();
  }
}
