import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { MRutasPartes } from './rutaspartes.class';

@Injectable({
	providedIn: 'root',
})
export class SSeccionespartesService {
	private subject = new Subject<any>();
	public datosEdicionSeccPartes: any;
	public DatosRutaParte: MRutasPartes;

	constructor(private http: HttpClient) {}

	// Paso del parámetro de la ruta
	// Validacion secciones
	setObsDatosSeleccionRP(vdatselecc: any): void {
		this.datosEdicionSeccPartes = vdatselecc;
	}

	// Datos del encabezado de la ruta parte
	setDatosRutaParte(PrmRutaParte: MRutasPartes): void {
		this.DatosRutaParte = PrmRutaParte;
	}

	// Paso del parámetro de la ruta
	setObsDatosSeccRP(prmRuta: string): void {
		this.subject.next(prmRuta);
	}

	getObsDatosSeccRP(): Observable<any> {
		return this.subject.asObservable();
	}
}
