import { Output, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { env } from 'src/app/config/env';
import { fnLocal } from 'src/app/_function/localStorage.fn';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

@Injectable()
export class MenuService {
  constructor(private http: HttpClient) {}
  list() {
    const url = `${env.url1}/aplication`;
    console.log(url)
    return this.http.get(url, { headers: { token: fnLocal.get('token') } }).pipe(
      catchError(err => {
        fnLocal.remove('menu')
        fnLocal.remove('token')
        window.location.href = '/'
        return throwError(err)
      })
    );
  }
}
