import { Component, OnInit } from '@angular/core';
import { Category, data } from '../../_function/pruebas.fn';
import RemoteFileSystemProvider from 'devextreme/file_management/remote_provider';

@Component({
	selector: 'app-matriz',
	templateUrl: './matriz.component.html',
	styleUrls: ['./matriz.component.scss'],
})
export class MatrizComponent {
	remoteProvider: RemoteFileSystemProvider;
	imageItemToDisplay: any = {};
	popupVisible = false;

	constructor() {
		this.remoteProvider = new RemoteFileSystemProvider({
			endpointUrl:
				'https://js.devexpress.com/Demos/Mvc/api/file-manager-file-system-images',
		});
		console.log(this.remoteProvider);
	}

	displayImagePopup(e): void {
		this.imageItemToDisplay = e.file;
		this.popupVisible = true;
	}
}
