import { Component, OnInit } from '@angular/core';
import { FnUserService } from 'src/app/service/service.index';
import { Bases_consto } from '../../_class/bcosto.class';
import notify from 'devextreme/ui/notify';
// import {  SocketService } from '../_ServiceSocket/socketConexion';

@Component({
  selector: 'app-bcostos',
  templateUrl: './bcostos.component.html',
  styleUrls: ['./bcostos.component.scss'],
})
export class BCostosComponent implements OnInit {
  companies: Bases_consto[];
  itemCount: number;
  company: Bases_consto;
  expression = false;
  items: Bases_consto;
  constructor(private _Bcosto: FnUserService) {}
  ngOnInit(): void {
    this.getCompanies();
  }
  getCompanies(): void {
    this._Bcosto.getDominios().subscribe(({ data, ok }: any) => {

      if (ok) {
        this.companies = data.todos;
        this.itemCount = data.length;
        data.todos.map((resp) => {
          if (resp.VALOR2 === data.CODIGO) {
            this.expression = true;
            this.items = resp;
          }
        });
      }
      // console.log(x);
      // this.companies = x.todos;
      // this.itemCount = x.length;
      // x.todos.map((resp) => {
      //   if (resp.VALOR2 === x.CODIGO) {
      //     this.expression = true;
      //     this.items = resp;
      //   }
      // });
    });
    // this.s.in.emit('consulta','get_dominios', (x: any) => {
    //   this.companies = x.todos;
    //   this.itemCount = x.length;
    //   x.todos.map(resp => {
    //     if(resp.VALOR2 === x.CODIGO){
    //       this.expression = true
    //       this.items = resp
    //     }
    //   })
    // })
  }
  getacction(e): void {
    this.expression = true;
    this.items = e.value;

    this._Bcosto
      .updateDominios(e.value.VALOR2, e.value.ID_GRUPO_DOMINIO, e.value.VALOR3)
      .then((resp: any) => {
        const { ok, data } = resp;
        if (ok) {
          notify(`Actualizar ${data.DESCRIPCION}`, 'success', 2000);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
}
